.banner {
    background-size: cover;
    background-image: url('https://image.tmdb.org/t/p/original/8Y43POKjjKDGI9MH89NW0NAzzp8.jpg');
    background-position: center center;
    height: 448px;
    color: white;
    object-fit: contain;
}

.banner__contents {
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.banner__title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner__description {
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
}

.banner__buttons {
    display: flex;
}

.banner__button {
    cursor: pointer;
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
}

.banner__button:hover {
    background-color: #e6e6e6;
    color: black;
    transition: all 0.2s;
}

.banner__button:active {
    transform: scale(0.95);
}

.banner--fadeBottom {
    height: 7.4rem;
    background-image: linear-gradient(
        180deg, 
        transparent,
        rgba(37, 37, 37, 0.61), 
        #111
        );
}


